.control-panel {
  display: flex;
  justify-content: center;
  width: 100%;
}

.select {
  width: 100%;
  height: 40px;
  padding: 0 10px;
  border: 2px solid black;
  cursor: pointer;
  margin-right: 10px;
}

.start-button {
  font-size: 16px;
  border-radius: 5px;
  padding: 10px 20px;
  transition: all 0.15s ease-in-out;
  color: white;
  background-color: #4560ff;
  border: none;
  cursor: pointer;
}

.start-button:hover {
  background-color: #4749ff;
}

.start-button:disabled {
  color: grey;
  background-color: white;
  border: 1px solid grey;
}
